input {
    border-radius: 0px;
}

.convertForm {
    display: flex;
    width: 100%;
    margin: 0 0 10px 0;
    position: relative;

    &__btn-select {
        display: flex;
        align-items: center;
        transition: 0.3s;
        border: 1px solid lightgray;
        box-shadow: 1px 1px 1px grey;
        padding: 0 10px;
        min-width: 60px;
        position: relative;
        color: rgb(50, 50, 50);
        font-weight: 500;
    }

    &__btn-select:hover {
        transition: 0.3s;
        background: lightgray;  
    }

    &__btn-select:hover {
        .convertForm__btn-icon {
            transition: 0.3s;
            transform: rotate(90deg);
        }
    }

    &__btn-icon {
        transition: 0.3s;
        width: 16px;
        height: 16px;
        position: absolute;
        top: 12px;
        right: 0;
    }

    &__input {
        width: 100%;
        border: 1px solid lightgray;
        box-shadow: 1px 1px 1px grey;
        padding: 10px 0 10px 10px;
        font-size: 16px;
    }

    &__selectList-hide {
        display: none;
    }

    &__selectList {
        position: absolute;
        top: 42px;
        right: 0;
        background: lightgrey;
        z-index: 3;
        max-height: 200px;
        overflow-y: scroll;
    }

    &__selectList li {
        margin: 5px;
    }

    &__selectList li input {
        cursor: pointer;
        background: lightgrey;
        color: grey;
        font-weight: 500;
    }

    &__selectList li input:hover {
        color: black;
    }

}