.content {

    &__col-main {
        border: 1px solid black;
        padding: 20px 5px;
        display: flex;

        flex-direction: column;

        h1 {
            font-weight: 600;
            font-size: 18px;
            text-align: center;
        }

        span {
            font-weight: 700;
            font-size: 18px;
            text-align: center;
        }
    }

    &__col {
        display: flex;
        width: 100%;
        margin: 0 0 10px 0;
        // align-items:;
    }

    &__row {
        display: flex;
        flex-direction: column;
    }

    &__submit {
        cursor: pointer;
        transition: 0.3s;
        padding: 10px;
        width: 100%;
        border: 1px solid lightgrey;
        box-shadow: 1px 1px 1px grey;
        font-size: 16px;
        font-weight: 500;
        color: rgb(50, 50, 50);
    }

    &__submit:hover {
        transition: 0.3s;
        background: lightgrey;
    }


}